import upng from 'upng-js'
export default {
	data() {
		return {
			id: '',
			points: [],
			canvasTxt: null,
			startX: 0,
			startY: 0,
			moveY: 0,
			moveX: 0,
			endY: 0,
			endX: 0,
			w: null,
			h: null,
			isDown: false,
			// 设备rpx比例
			pixelScale: 1
		}
	},
	onLoad (q) {
		this.id = parseInt(q.id)
	},
	mounted() {
		this.canvasTxt = uni.createCanvasContext('signatureCanvas')
		this.getPixelScale()
	},
	methods: {
		// 获取设备rpx比例
		getPixelScale () {
			uni.getSystemInfo({
				success: res => {
					this.pixelScale = res.windowWidth / 750
				}
			})
		},
		//移动设备事件
		touchStart(ev) {
			ev = ev || event
			ev.preventDefault()
			// console.log(ev)
			if (ev.touches.length == 1) {
				let obj = {
					x: ev.touches[0].x,
					y: ev.touches[0].y
				}
				this.startX = obj.x
				this.startY = obj.y
				this.canvasTxt.beginPath()
				this.canvasTxt.moveTo(this.startX, this.startY)
				this.canvasTxt.lineTo(obj.x, obj.y)
				this.canvasTxt.closePath()
				this.canvasTxt.stroke()
				this.canvasTxt.draw(true)
				this.points.push(obj)
			}
		},
		//移动设备事件
		touchMove(ev) {
			ev = ev || event
			ev.preventDefault()
			// console.log(ev);
			if (ev.touches.length == 1) {
				let obj = {
					x: ev.touches[0].x,
					y: ev.touches[0].y
				};
				this.moveY = obj.y
				this.moveX = obj.x
				// this.canvasTxt.beginPath();
				this.canvasTxt.moveTo(this.startX, this.startY)
				this.canvasTxt.lineTo(obj.x, obj.y)
				this.canvasTxt.closePath()
				this.canvasTxt.stroke()
				this.canvasTxt.draw(true)
				this.startY = obj.y
				this.startX = obj.x
				this.points.push(obj)
			}
		}, 
		//移动设备事件
		touchEnd(ev) {
			ev = ev || event
			ev.preventDefault()
			// console.log(ev);
			if (ev.touches.length == 1) {
				let obj = {
					x: ev.touches[0].x,
					y: ev.touches[0].y
				};
				this.canvasTxt.beginPath()
				this.canvasTxt.moveTo(this.startX, this.startY)
				this.canvasTxt.lineTo(obj.x, obj.y)
				this.canvasTxt.closePath()
				this.canvasTxt.stroke()
				this.canvasTxt.draw(true)
				this.points.push(obj)
				this.points.push({ x: -1, y: -1 })
			}
		}, //重写
		overwrite() {
			// this.canvasTxt.clearRect(0,0, 150, 75)
			this.canvasTxt.draw()
			this.points = []
		},
		// 提交签名
		submitSignature () {
			console.log()
			uni.canvasGetImageData({
			  canvasId: 'signatureCanvas',
			  x: 0,
			  y: 0,
			  width: 750 * this.pixelScale,
			  height: 500 * this.pixelScale,
			  success: res => {
					this.$JRequest('enterBenefit', {
						caseId: this.id,
						sign: 'data:image/png;base64,' + uni.arrayBufferToBase64(upng.encode([res.data.buffer], res.width, res.height, 0))
					})
					this.$JFn.showSuccess('提交成功')
					uni.reLaunch({
						url: '/pages/my/benefit/index'
					})
			  },
				fail: () => {
					console.log('提交失败')
					this.$JFn.showError('提交失败')
				}
			})
		}
	}
}
